// Centered container element
@mixin container-fixed($gutter: $grid-gutter-width) {
  margin-right: auto;
  margin-left: auto;
  padding-left: $gutter / 2;
  padding-right: $gutter / 2;
  @include clearfix; }

// Creates a wrapper for a series of columns
@mixin make-row($gutter: $grid-gutter-width) {
  margin-left: $gutter / -2;
  margin-right: $gutter / -2;
  @include clearfix; }

// Generate the extra small columns
@mixin make-xs-column($columns, $column-number: $grid-columns, $gutter: $grid-gutter-width) {
  position: relative;
  float: left;
  width: percentage($columns / $column-number);
  min-height: 1px;
  padding-left: $gutter / 2;
  padding-right: $gutter / 2; }

// Generate the small columns
@mixin make-sm-column($columns, $column-number: $grid-columns, $gutter: $grid-gutter-width) {
  position: relative;
  min-height: 1px;
  padding-left: $gutter / 2;
  padding-right: $gutter / 2;
  @media (min-width: $screen-sm-min) {
    float: left;
    width: percentage($columns / $column-number); } }

// Generate the medium columns
@mixin make-md-column($columns, $column-number: $grid-columns, $gutter: $grid-gutter-width) {
  position: relative;
