// Generate the large columns
@mixin make-lg-column($columns, $column-number: $grid-columns, $gutter: $grid-gutter-width) {
  position: relative;
  min-height: 1px;
  padding-left: $gutter / 2;
  padding-right: $gutter / 2;
  @media (min-width: $screen-lg-min) {
    float: left;
    width: percentage($columns / $column-number); } }
